.button{font-size:20px;font-weight:500;color:#000;letter-spacing:1pt;font-family:"Open Sans",sans-serif;display:flex;justify-content:center;align-items:center;width:220px;max-width:50vw;height:70px;max-height:20vw;outline:none;transition:500ms}@media (max-width:400px){.button{font-size:15px}}.button:hover{cursor:pointer}.button_small{font-size:18px;width:auto;height:55px;padding:0 20px}.button_white{background-color:transparent;border:2px solid white;color:white}@media screen and (min-width:800px){.button_white:hover{background-color:white;border:2px solid white;color:black}}.button_seagreen{background-color:#00897b;border:none;color:white}@media screen and (min-width:800px){.button_seagreen:hover{background-color:transparent;border:2px solid #00897b;color:#00897b}}.button_black{background-color:transparent;border:2px solid black;color:black}@media screen and (min-width:800px){.button_black:hover{background-color:#00897b;border:none;color:white}}.button_seagreen-black{background-color:transparent;border:2px solid #00897b;color:black}@media screen and (min-width:800px){.button_seagreen-black:hover{background-color:#00897b;border:2px solid #00897b;color:white}}